.memo {
  position: relative;
  width: 29%;
  background: #fff;
  margin: 10px;
  padding: 0 15px;
  border: 1px solid #DDD;
  border-radius: 3px;
  transition: all .3s;
  line-height: 1.8;
}
.memo:hover {
  box-shadow: 0 0 20px #ddd;
}
.memo:hover .memo-date {
  cursor: default;
  font-size: 14px;
  color: #ACE4DF;
}
.memo .memo-date {
  position: absolute;
  bottom: 2px;
  right: 5px;
  font-size: 12px;
  color: #f6f6f6;
  transition: color, font-size 0.3s, 0.3s ease-in-out;
}



