.margin-tb-zero,
.markdown-body ol ol,
.markdown-body ul ol,
.markdown-body ol ul,
.markdown-body ul ul,
.markdown-body ol ul ol,
.markdown-body ul ul ol,
.markdown-body ol ul ul,
.markdown-body ul ul ul {
  margin-top: 0;
  margin-bottom: 0;
}
.markdown-body {
  font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, freesans, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 16px;
  color: #333;
  line-height: 1.6;
  word-wrap: break-word;
  padding: 45px;
  background: #fff;
  border: 1px solid #ddd;
  -webkit-border-radius: 0 0 3px 3px;
  border-radius: 0 0 3px 3px;
}
.markdown-body > *:first-child {
  margin-top: 0 !important;
}
.markdown-body > *:last-child {
  margin-bottom: 0 !important;
}
.markdown-body * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.markdown-body h1,
.markdown-body h2,
.markdown-body h3,
.markdown-body h4,
.markdown-body h5,
.markdown-body h6 {
  margin-top: 1em;
  margin-bottom: 16px;
  font-weight: bold;
  line-height: 1.4;
}
.markdown-body p,
.markdown-body blockquote,
.markdown-body ul,
.markdown-body ol,
.markdown-body dl,
.markdown-body table,
.markdown-body pre {
  margin-top: 0;
  margin-bottom: 16px;
}
.markdown-body h1 {
  margin: 0.67em 0;
  padding-bottom: 0.3em;
  font-size: 2.25em;
  line-height: 1.2;
  border-bottom: 1px solid #eee;
}
.markdown-body h2 {
  padding-bottom: 0.3em;
  font-size: 1.75em;
  line-height: 1.225;
  border-bottom: 1px solid #eee;
}
.markdown-body h3 {
  font-size: 1.5em;
  line-height: 1.43;
}
.markdown-body h4 {
  font-size: 1.25em;
}
.markdown-body h5 {
  font-size: 1em;
}
.markdown-body h6 {
  font-size: 1em;
  color: #777;
}
.markdown-body ol,
.markdown-body ul {
  padding-left: 2em;
}
.markdown-body ol ol,
.markdown-body ul ol {
  list-style-type: lower-roman;
}
.markdown-body ol ul,
.markdown-body ul ul {
  list-style-type: circle;
}
.markdown-body ol ul ul,
.markdown-body ul ul ul {
  list-style-type: square;
}
.markdown-body ol {
  list-style-type: decimal;
}
.markdown-body ul {
  list-style-type: disc;
}
.markdown-body blockquote {
  margin-left: 0;
  margin-right: 0;
  padding: 0 15px;
  color: #777;
  border-left: 4px solid #ddd;
}
.markdown-body table {
  display: block;
  width: 100%;
  overflow: auto;
  word-break: normal;
  word-break: keep-all;
  border-collapse: collapse;
  border-spacing: 0;
}
.markdown-body table tr {
  background-color: #fff;
  border-top: 1px solid #ccc;
}
.markdown-body table tr:nth-child(2n) {
  background-color: #f8f8f8;
}
.markdown-body table th,
.markdown-body table td {
  padding: 6px 13px;
  border: 1px solid #ddd;
}
.markdown-body pre {
  word-wrap: normal;
  padding: 16px;
  overflow: auto;
  font-size: 85%;
  line-height: 1.45;
  background-color: #f7f7f7;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}
.markdown-body pre code {
  display: inline;
  max-width: initial;
  padding: 0;
  margin: 0;
  overflow: initial;
  font-size: 100%;
  line-height: inherit;
  word-wrap: normal;
  white-space: pre;
  border: 0;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  background-color: transparent;
}
.markdown-body pre code:before,
.markdown-body pre code:after {
  content: normal;
}
.markdown-body code {
  font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
  padding: 0;
  padding-top: 0.2em;
  padding-bottom: 0.2em;
  margin: 0;
  font-size: 85%;
  background-color: rgba(0, 0, 0, 0.04);
  -webkit-border-radius: 3px;
  border-radius: 3px;
}
.markdown-body code:before,
.markdown-body code:after {
  letter-spacing: -0.2em;
  content: "\00a0";
}
.markdown-body a {
  color: #4078c0;
  text-decoration: none;
  background: transparent;
}
.markdown-body img {
  max-width: 100%;
  max-height: 100%;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  -webkit-box-shadow: 0 0 10px #555;
  box-shadow: 0 0 10px #555;
}
.markdown-body strong {
  font-weight: bold;
}
.markdown-body em {
  font-style: italic;
}
.markdown-body del {
  text-decoration: line-through;
}
.task-list-item {
  list-style-type: none;
}
.task-list-item input {
  font: 13px/1.4 Helvetica, arial, nimbussansl, liberationsans, freesans, clean, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  margin: 0 0.35em 0.25em -1.6em;
  vertical-align: middle;
}
.task-list-item input[disabled] {
  cursor: default;
}
.task-list-item input[type="checkbox"] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}
.task-list-item input[type="radio"] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}
/*html5*/
article,
aside,
dialog,
footer,
header,
main,
section,
footer,
nav,
figure,
menu {
  display: block;
}
/*文字选中的颜色*/
::-moz-selection {
  background: #ACE4DF;
  color: #FEFEFE;
}
::-webkit-selection {
  background: #ACE4DF;
  color: #FEFEFE;
}
::selection {
  background: #ACE4DF;
  color: #FEFEFE;
}
/*小屏*/
@media all and (max-width: 720px) {
  nav {
    display: none;
  }
  .info-message,
  .info-photo {
    width: 100%;
  }
  .skill-badge {
    margin: 10px 5px;
    width: 80px;
    height: 80px;
  }
}
/*大屏*/
@media all and (min-width: 720px) {
  nav {
    display: inline-block;
  }
  .skill-badge {
    margin: 25px;
    width: 80px;
    height: 80px;
  }
}
body {
  margin: 0;
  padding: 0;
  background-color: #FEFEFE;
  color: #333;
  font-size: 14px;
  font-family: "Microsoft YaHei", "Verdana", "sans-serif";
}
main {
  display: flex;
  flex-flow: row wrap;
  margin-top: 63px;
  margin-left: 20%;
  padding: 20px;
  transition: margin-top 0.5s;
}
.right {
  margin-left: 25%;
  padding: 10px;
}
.right .post {
  width: 80%;
  margin: 35px auto;
  padding: 8px 30px;
  background: #fefaf6;
}
.right .post-title a {
  color: #404040;
  text-decoration: none;
  transition: color .3s;
}
.right .post-title a:hover {
  color: #ACE4DF;
}
/*PC设备*/
@media all and (min-width: 1024px) {
  body {
    min-width: 1200px;
  }
  .memo-main {
    padding-right: 15%;
  }
  .memo-col {
    width: 30%;
  }
}
/*移动设备*/
@media all and (max-width: 1023px) {
  .memo-col {
    width: 100%;
  }
  .memo-main {
    padding-right: 5%;
  }
  .memo-anchor {
    display: none;
  }
  .info-wrapper {
    position: static;
    border: none;
    width: 100%;
  }
  .right {
    margin-left: 0;
  }
}
/* MEMO */
.memo-main {
  padding-left: 5%;
  line-height: 1.6;
  transition: background .8s;
}
.memo-main.hide-top {
  background: #000;
}
.memo-main pre {
  white-space: normal;
  word-break: break-word;
  font-family: sans-serif;
  background: #f6f6f6;
  border-radius: 2px;
  padding: 5px 10px;
  border-left: 3px solid #b9b9b9;
}
.memo-main code {
  background: #f6f6f6;
  border-radius: 2px;
  padding: 5px 10px;
  word-break: break-all;
  font-family: -webkit-pictograph, "Microsoft YaHei", Verdana, sans-serif;
}
.memo-main a {
  color: #ff1493;
  transition: color .3s;
}
.memo-main a:link {
  color: #ff1493;
}
.memo-main a:visited {
  color: #ff90cd;
}
.memo-main a:hover {
  color: #ff1493;
}
.memo-main .memo-col {
  display: inline-block;
  vertical-align: top;
}
.memo-main .memo-card {
  position: relative;
  width: 80%;
  background: #fff;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 10px 20px 20px;
  border: 1px solid #DDD;
  border-radius: 3px;
  transition: all .3s;
  line-height: 1.8;
}
.memo-main .memo-card:hover {
  box-shadow: 0 0 20px #ddd;
}
.memo-main .memo-card:hover .memo-time {
  cursor: default;
  font-size: 14px;
  color: #ACE4DF;
}
.memo-main .memo-time {
  position: absolute;
  bottom: 2px;
  right: 5px;
  font-size: 12px;
  color: #f6f6f6;
  transition: color, font-size 0.3s, 0.3s ease-in-out;
}
.memo-anchor {
  position: fixed;
  right: 5%;
  top: 95px;
}
.memo-anchor ul {
  list-style-type: none;
  padding: 0;
}
.memo-anchor li {
  height: 30px;
  border-left: 1px solid #DDD;
  transition: all .3s;
}
.memo-anchor li:hover {
  border-left-color: #81D8D0;
}
.memo-anchor a {
  color: #ABABAB;
  padding: 0 20px 10px;
  text-decoration: none;
  font-size: 16px;
  transition: all .3s;
}
.memo-anchor a:hover {
  color: #81D8D0;
}
.memo-anchor-target {
  position: relative;
  padding: 5px 0 5px 5px;
  margin: 10px 0;
  font-size: 16px;
  color: #333;
}
.memo-anchor-target::before {
  content: "#";
  color: #81D8D0;
  position: absolute;
  left: -10px;
  bottom: 2px;
  font-size: 1.2em;
  font-weight: 700;
}
.hide-top .memo-anchor-target {
  color: #FEFEFE;
}
.memo-anchor-target .fake-target {
  position: relative;
  top: -70px;
}



