.my-md pre {
  margin: 0;
  padding: 0;
  color: #383a42;
  background: #fafafa;
}
.my-md code {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  color: #383a42;
  background: #fafafa;
}
.my-md a {
  color: #ff1493;
  transition: color .3s;
}
.my-md a:link {
  color: #ff1493;
}
.my-md a:visited {
  color: #ff90cd;
}
.my-md a:hover {
  color: #ff1493;
}



